.qrcode-icon i{
  font-size: 20px;;
}
small {
  color: $darkgray;
}
.pic-info{
  height: 80px;
  max-height: 80px;
  overflow: hidden;
  padding-top: 15px;
  img {
    border-radius: 10px;
  }
}

.accounts-block-title{
  padding-top: 25px;
  .account-list{
    height: auto;
    max-height: 600px;
    overflow: hidden;
    overflow-y: scroll;
  }
  span{
    font-size: 15px;
    color: $main-color;
  }
  ul{
    padding-left: 0px;
    list-style: none;
    margin-top: 10px;
    h4, p{
      margin: 0px;
    }
    small{
      color: $darkgray;
    }
    .panel-body{
      background-color: #F5F5F5;
      padding: 10px;
      box-shadow: $shadow;
    }
    .value{
      font-weight: bold;
      font-size: 12px;
      color:#000;
    }
  }
}

.menu-logo{
  position: absolute;
  bottom: 10px;
  left: 30px;
}

.equity-block{
  padding: 10px 0px;
  color: white;
  border-radius: 5px;
  margin: 0px;
  box-shadow: $shadow;
  background: linear-gradient(to top left, #cba894 0%, #042b48 100%);
  .balance-info{
    // height: 105px;
    max-height: 110px;
    overflow: hidden;
    padding-top: 15px;
    h3{
      // margin-top: 10px;
      margin: 0px;
    }
    small{
      color: #d4d4d4;
    }
  }
  ul{
    margin-top: 15px;
    padding: 0px;
    list-style: none;
    li{
      margin: 10px 0px;
    }
  }
}
.copied-msg{
  background-color: green;
  padding-top: 10px;
  display: none;
  color: white;
}
.copy-txt{
  cursor: pointer;
}

.history-label{
  margin-top: 15px;
}

.history-list{
  height: auto;
  max-height: 600px;
  overflow: hidden;
  overflow-y: scroll;
  .cash-out{
    border:1px solid #eee;
    border-left:4px solid $main-color;
    padding: 8px;
    margin-top: 15px;
    display: block;
    background-color: #f6f6f6;
    box-shadow: $shadow;
    table{
      width: 100%
    }
    .date{
      width: 15%;
      padding-right: 5px;
    }
    .content{
      font-size: 12px;
      width: 60%;
      span{
        padding: 0px 5px;
      }
    }
    .amount{
      font-size: 13px;
      font-weight: bold;
      color:$main-color;
      width: 25%;
      text-align: right;
    }
  }
  .cash-in{
    border:1px solid #eee;
    border-left:4px solid green;
    padding: 8px;
    margin-top: 15px;
    display: block;
    background-color: #f6f6f6;
    box-shadow: $shadow;
    table{
      width: 100%
    }
    .date{
      width: 15%;
      padding-right: 5px;
    }
    .content{
      font-size: 12px;
      width: 60%;
      span{
        padding: 0px 5px;
      }
    }
    .amount{
      font-size: 13px;
      font-weight: bold;
      color:green;
      width: 25%;
      text-align: right;
    }
  }
}
.sell{
  color: $main-color;
}
.buy{
  color: green;
}
