$main-color: #a41638; // main color
$second-color:gray;
$third-color:rgb(48,48,48); // light black
$white-color:#fff;
$orange-color:rgb(248,145,37); // orange color
$darkgray:#a9a9a9;
$full-width:100%;
$shadow: 0px 6px 5px 0px #a2a2a2;

html {
    height: 100%
}

body{
  margin: 0px;
  padding: 0px;
  font-family: sans-serif !important;
  font-size: 12px;
}

a,a:visited,a:hover,a:active{
  color: #505154;
  text-decoration: none;
}

//main menu
.navbar{
  padding: 0px 15px;
  color: $main-color;
  margin-bottom: 0px;
  position: -webkit-sticky;
  position: sticky;
  z-index: 9999;
  top: 0px;
  background-color: #fff;
  .openNavBtn{
    font-size:30px;
    cursor:pointer;
    margin-top: 14px;
  }
  .page-title{
    line-height: 3.5;
    font-size: 17px;
  }
}
.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  transition: 0.3s;
  padding-top: 60px;
  small{
    padding-left:10px;
  }
  .closeNavBtn{
    border: none;
  }
}

.sidenav a , .sidenav span {
  padding: 15px 8px 15px 32px;
  text-decoration: none;
  font-size: 11px;
  color: #818181;
  display: block;
  transition: 0.3s;
  border-bottom: 1px solid #818181;
}

.sidenav a:hover {
  color: #f1f1f1;
}

.sidenav .closeNavBtn {
  position: absolute;
  cursor: pointer;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

a .fa{
  padding-right: 10px;
}

.tab-active{
  background: $main-color;
  color: $white-color!important;
}

.contact-us{
    margin: 7px 0px 7px 28px;
}

//Message box
.msg-box{
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  position: fixed;
  top: 0px;
  z-index: 9999;
  -webkit-animation-name: fadeIn; /* Fade in the background */
  -webkit-animation-duration: 0.4s;
  animation-name: fadeIn;
  animation-duration: 0.4s;
  .msg-content{
    background-color: $white-color;
    border-radius: 5px;
    margin: 15px;
    padding: 15px;
    position: relative;
    top: 39%;
  }
  .msg-options button{
    width: 40%;
  }
}

//loading box
.loading-box{
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  position: fixed;
  top: 0px;
  z-index: 9999;
  -webkit-animation-name: fadeIn; /* Fade in the background */
  -webkit-animation-duration: 0.4s;
  animation-name: fadeIn;
  animation-duration: 0.4s;
  .loading-content{
    background-color: $white-color;
    padding: 1px 0px;
  }
}

// Modal
/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 99999; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  -webkit-animation-name: fadeIn; /* Fade in the background */
  -webkit-animation-duration: 0.4s;
  animation-name: fadeIn;
  animation-duration: 0.4s
}

/* Modal Content */
.modal-content {
  position: fixed;
  bottom: 0;
  background-color: #fefefe;
  width: 100%;
  -webkit-animation-name: slideIn;
  -webkit-animation-duration: 0.4s;
  animation-name: slideIn;
  animation-duration: 0.4s
}

/* The Close Button */
.close {
  color: $second-color;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.modal-header {
  padding: 2px 16px;
  background-color: transparent;
  border:none;
}

.modal-body {
  padding: 2px 16px;
  ul{
    padding: 0px;
    list-style: none;
    li{
      padding: 20px 0px;
      border-bottom: 1px solid $darkgray;
    }
    li:last-child{
      border-bottom:none;
    }
  }
}

.modal-footer {
  padding: 2px 16px;
  background-color: #5cb85c;
  color: white;
}

.back-btn{
  color: $main-color!important;
  line-height: 4.5;
}

.change-pass-btn{
    background: linear-gradient(to right, #a41638 0%, #000 100%);
    color: #fff;
    border-radius: 30px;
    border: 1px solid transparent;
    outline: none;
    font-size: 17px;
    font-weight: 100;
    box-shadow: 0px 10px 6px #c7c7c7;
    width: 90%;
    padding: 10px 0px;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    margin: 24px auto;
}

.success-pass{
    border-color: green;
}
.wrong-pass{
    border-color: red;
}

/* Add Animation */
@-webkit-keyframes slideIn {
  from {bottom: -300px; opacity: 0}
  to {bottom: 0; opacity: 1}
}

@keyframes slideIn {
  from {bottom: -300px; opacity: 0}
  to {bottom: 0; opacity: 1}
}

@-webkit-keyframes fadeIn {
  from {opacity: 0}
  to {opacity: 1}
}

@keyframes fadeIn {
  from {opacity: 0}
  to {opacity: 1}
}

@media screen and (max-height: 450px) {
  .sidenav {padding-top: 15px;}
  .sidenav a {font-size: 18px;}
}
