.deposit-form{
  label{
    color: $main-color;
    font-size: 11px;
  }
  select.minimal{
    /* styling */
    background-color: white;
    display: inline-block;
    line-height: 1.5em;
    padding: 0.5em 3.5em 0.5em 1em;
    width: 100%;
    border-radius: 5px;
    /* reset */

    margin: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;

    background-image:
      linear-gradient(45deg, transparent 50%, $main-color  50%),
      linear-gradient(135deg, $main-color 50%, transparent 50%),
      linear-gradient(to right, #ccc, #ccc);
    background-position:
      calc(100% - 20px) calc(1em + 2px),
      calc(100% - 15px) calc(1em + 2px),
      calc(100% - 2.5em) 0.5em;
    background-size:
      5px 5px,
      5px 5px,
      1px 1.5em;
    background-repeat: no-repeat;
  }

  input[type=submit]{
    background: linear-gradient(to right, $main-color 0%, #000000 100%);
    color: #fff;
    border-radius: 30px;
    border: 1px solid transparent;
    outline: none;
    font-size: 17px;
    font-weight: 100;
    box-shadow: 0px 10px 6px #c7c7c7;
    width: 90%;
    padding: 10px 0px;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    margin: 24px auto;
  }

  .amount-field{
    height: 60px;
    font-size: 30px;
  }
}

select:-moz-focusring {
  color: transparent;
  text-shadow: 0 0 0 #000;
}
